.button {
    margin-left: 20px;
    height: 50px;
    border-radius: 5px;
    cursor: pointer;
    background-color:rgb(255, 116, 65);
    font-size: 1.5rem;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
}

.button:hover {
    background-color:rgb(248, 174, 146);
}